.navwrap{
    display: flex;
    flex-flow: row nowrap;
    justify-content: space-between;
    align-items: center;
    left: 0;
    top: 0;
    margin: 0 auto;
    width: 100vw;
    z-index: 10;
    position: fixed;
}
.brandc{
    z-index: 10;
}
.brand{
    margin-left: 10px;
    color: #AEA0DE;
    font-size: 3rem;
    padding: 0;
    cursor: pointer;
}
.links{
    display: flex;
    flex-flow: column wrap;
    margin-right: 15px;
}
.bar{
    cursor: pointer;
    display: flex;
    flex-flow: column wrap-reverse;
    z-index: 10;
    background: transparent;
    border: none;
}
#bar1,#bar2,#bar3{
    background-color:  #AEA0DE;
    border-radius: 5px;
    border: none;
    overflow: none;
    height: 3px;
    margin-bottom:0.5rem ;
}
#bar1{
    width: 2rem;
}
#bar2{
    width: 2rem;
}
#bar3{
    height: 3px;
    width: 2rem;
}

.nav-link{
    text-decoration: none;
    list-style-type: none;
    cursor: pointer;
    padding-top: 30px;
    padding-bottom: 30px;
    color: #AEA0DE;
}
.nav-link:hover{
    background-color: #1f1c2b;
    transition: 0.4s;
    color: #8278a3;   
    letter-spacing: 1px;
}
.nav-links{
    display: none;
    padding: 0;
    flex-flow: column wrap;
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    width: 100%;
    height: 50vh;
    margin: 55px auto;
    text-align: center;
    font-size: 2rem;
    font-weight: 300;
    background-color: #2D283E;
    z-index: 10;
}
