.por_wrap{
    width: 100vw;
    min-height: 95vh;
    display: flex;
    flex-flow: column wrap;
    margin-top: 70px;
    justify-content: space-between;
}
.cont{
    width: 100%;
    height: 100%;
    display: flex;
    flex-flow: row wrap;
}
.imgs{
    width: 55%;
    height: 500px;
    margin-left: 5%;
}
#por_img{
    background-image: url('../images/portfolio.png');
    background-size: contain;
    background-position: center;
    background-repeat: no-repeat;
    border: 2px solid #AD67F3;
}
#pp_img{
    background-image: url('../images/bg.jpg');
    background-size: cover;
    background-position: center;
}
.project{
    width: 35%;
    display: flex;
    flex-flow: column wrap;
    justify-content: space-between;
}
.project_img::before{
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 200px;
    background-color: rgba(0, 0, 0, 0.5);
    z-index: 0;
}
.project_img{
    position: relative;
    width: 65%;
    height: 200px;
    border: 4px solid #AD67F3;
    margin: -15px auto;
    text-align: center;
    line-height: 200px;
}

.project_img > p{
    transform: rotate(0deg);
    font-size:3rem;
    color: #AD67F3;
    z-index: 1000000;
}
.project_img>*{
    z-index: 10;
}
.project_text{
    color: #AD67F3;
    width: 70%;
    text-align: center;
    font-size: 1.4rem;
    margin: 0 auto;
}
.back{
    display: flex;
    width: 100%;
    flex-flow: row wrap;
    align-items: center;
    justify-content: flex-start;
}
.mywork{
    width: 50%;
}
.back-btn{
    margin-bottom: 20px;
    margin-left: 20px;
    font-size: 3rem;
    color: #AD67F3;
    font-weight: bold;
    cursor: pointer;
}
@media screen and (max-width: 900px) {
    .imgs{
        width: 90%;
        height: 200px;
    }
    .project{
        width: 100%;
    }
    .project_text{
        width: 80%;
    }
    .project_img{
    margin: 10px auto;
    line-height: normal;
    display: flex;
    align-items: center;
    justify-content: center;
    }

}