.homewrap{
    width: 100vw;
    min-height: 95vh;
    display: flex;
    flex-flow: column wrap;
    justify-content: center;
    align-items: center;
    
}
.homecnt{
    width: 100%;
    height: 100%;
    text-align: center;
    z-index: 5;
}
.ui{
    font-size: 10vw;
    letter-spacing: 1px;
    text-align: left;
    margin-left: 15px;
    color: #AEA0DE;
    margin-bottom: 1%;
}
.milica{
    font-weight: bold;
    text-align: right;
    margin-right: 10px;
    color: #AD67F3;
    font-size: 18vw;
    letter-spacing: 1px;
    z-index: 1;
}
.box{
    width: 61.5%;
    height: 50%;
    position: absolute;
    z-index: 0;
    left: 5%;
    background: url("../images/bg.jpg");
    background-size: cover;
    background-position: center;
    border: 2px solid #AD67F3;
    margin-bottom: 5%;
}
.box::before{
    content: '';
    position: absolute;
    top:0;
    left:0;
    background-color: rgba(0, 0, 0, 0.527); 
    width: 100%;
    height: 100%;   
}
.scrolld{
    height: 40px;
    display: flex;
    flex-flow: column nowrap;
    align-items: center;
    position: relative;
    bottom: 10px;
    cursor: pointer;
}
.scrolls{
    fill: #AD67F3;
    border-radius: 50%;
    padding: 5px;
    transform: scale(1.4);
}

@media screen and (max-width:1000px) {
    .box{
        width: 59%;
        left: 6%;
    }
}