.contwrap{
    width: 100vw;
    min-height: 100vh;
    text-align: center;
    font-size: 3.2rem;
    font-weight: 400;
    color: #AEA0DE;
    display: flex;
    justify-content: center;
}   
.cont_text{
    width: 75%;
    margin-top: 80px;
}
.cont_m{
    position: relative;
}
.mil{
    position: absolute;
    bottom: 30px;
    right: -140px;
    font-size: 5rem;
    font-weight: bold;
    color:#AD63F3;    
    text-shadow: -1px 3px 0px #4e306c;
}
@media screen and (max-width: 900px) {
    .contwrap{
        font-size: 2.3rem;
    }
    .cont_text{
        width: 95%;
    }
    .mil{
        font-size: 4rem;
        right: 0;
    }
}
