#food_img{
    width: 50%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}
.phone{
    width: 35%;
}
.phone_bg{
    width: 100%;
    height: 110%;
    background-image: url('../images/projects/food1.png');
    background-size: cover ;
    background-position: center;
}
.arrow{
    color: #AD67F3;
    font-size: 5rem;
    transform: scaley(1.5);
    cursor: pointer;
    -webkit-user-select: none;        
    -moz-user-select: none;
    -ms-user-select: none; 
    user-select: none; 
}
#pfd_img{
    background-image: url('../images/food.png');
    background-size: cover;
    background-position: center;
}
.pfd_text{
 line-height: normal;
 margin-top: 50px;
 
}
.pt_food{
    margin-top: 100px;
    width: 150%;
    margin-left:-100px ;
}
.css-1rle95y{
    background: transparent !important;
}
@media screen and (max-width: 900px) {
    .pfd_text{
        margin-top: 0;
    }
    .pt_food{
        width: 100%;
        margin-left: 0;
    }
    .phone{
        width: 500px;
    }
}