.aboutwrap{
    width: 100vw;
    min-height: 90vh;
    margin: 0;
    padding: 0;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 0;
    margin-top:70px;
    justify-content: center;
    align-items: center;
}
.bgab{
    background:url('../images/bg.jpg');
    background-size: cover;
    background-position: center;
    width: 60%;
    height: 500px;
    position: absolute;
    left: 5%;
    z-index: 0;
    border: 2px solid #AD67F3;
}
.skills{
    grid-column-start: 1;
    grid-column-end: 1;
    grid-row-start: 1;
    color: white;
    z-index: 1;
    position: relative;
    font-size: 4rem;
    margin-left: 4px;
    margin-top: 67%;
    color:#AD67F3;
}
.about{
    grid-column-start: 2;
    color: white;
    z-index: 1;
    text-align: left;
    justify-self:flex-end;
    overflow: hidden;
    margin-right: 17px;
    font-size: 4rem;
    margin-bottom: auto;
    margin-top: 7%;
    color:#AD67F3;
}
.sklist{
    margin-left: 21.5%;
    font-size: 2rem;
}
.abouttxt{
    grid-column-start: 3;
    grid-column-end: 3;
    width: 90%;
    grid-row-start: 1;
    grid-row-end: 2;
    color: white;
    font-size: 2rem;
    text-align: justify;
    color:#AD67F3;
}

@media screen and (max-width: 900px) {
 .aboutwrap{
    display: flex;
    flex-flow: column wrap;
    margin-top: 0px;
 }   
 .abouttxt{
    z-index: 1;
 }
 .bgab{
    width: 90%;
 }
 .about{
    position: relative;
    top: 180px;
 }
 .abouttxt{
    position: relative;
    top: 200px;
 }
 .skills{
    margin-top: 240px;
 }
 #aboutscroll{
    position: relative;
    bottom: -250px;
 }

}