body{  
    background-color: #2D283E;
    overflow-x: hidden;
}
::-webkit-scrollbar {
  width: 5px;
}
::-webkit-scrollbar-track {
  background: #2D283E;
}
::-webkit-scrollbar-thumb {
  background: #AD67F3;
  border-radius: 5px;
}
::-webkit-scrollbar-thumb:hover {
  background: #533075;
}   