.workwrap{
    width: 100vw;
    min-height: 90vh;
    display: flex;
    flex-flow: row wrap;
    justify-content: center;
    margin-top: 80px;
}
.top_row, .bot_row{
    display: flex;
    flex-flow:row wrap;
    justify-content: space-between;
    width: 100%;
}
.work::before{
    content: '';
    position: absolute;
    top: -1px;
    left: 0;
    width: 100%;
    height: 201px;
    background-color: rgba(0, 0, 0, 0.6);
}
.work > p{
    transform:rotate(0deg);
    color: #b76eff;
}
.work{
    position: relative;
    width: 310px;
    height: 200px;
    background-size: cover;
    background-position: center;
    margin-right: 20px;
    text-align: center;
    border: 2px solid #AD67F3;
    color: white;
    font-size: 4rem;
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
}
#work1{
    background:url(../images/bg.jpg);
}
#work2{
    background: url(../images/logo.png);
}
#work3{
    background: url(../images/singup.png);
}
#work3 > p{
    line-height: normal;
    margin-top:15px;
}

#work4{
    background: url(../images/dating.png);
}
#work5{
    background: url(../images/front.png);
}
#work6{
    background: url(../images/food.png);
}
#work1, #work2, #work3, #work4, #work5, #work6{
    background-size: cover;
    background-position: center;
}
.middle_text{
    position: relative;
    width: 100%;
    text-align: center;
}
.middle_text > p{
    font-size: 15rem;
    letter-spacing: 40px;
    font-weight: bold;
    color: #AD67F3;

}
.work:first-child{
    margin-left: 2%;
}
#work2, #work5{
    position: relative;
    bottom: 30px;
}
#work6{
    font-size: 3.6rem;
}
@media screen and (max-width: 900px) {
    .workwrap{
        margin-top: 300px;
    }
    .work{
        width: 94vw;
        margin-bottom: 15px;
    }
    .middle_text > p{
        font-size: 5.5rem;
        letter-spacing: 5px;
    }
    #work2, #work5{
        bottom: 0;
    }
}